<template>
  <div class="adddetailWrap">
      <header>
          <router-link to="/newaddress">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>搜索地址</div>
      </header>
      <section>
          <div class="add-detail">
              <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="inputV">
              <button @click="searchAdd">确认</button>
          </div>
          <div class="warnpart">为了满足商家的送餐要求，建议您从列表中选择地址</div>
          <section class="poisearch-container">
              <ul>
                  <li v-for="p in placeArr" :key='p.name' @click="chenckAdd(p)">
                      <p class="p1">{{p.name}}</p>
                      <p>{{p.address}}</p>
                  </li>
              </ul>
          </section>
          <div class="point" v-if="isShow">
              <p>找不到地址？</p>
              <p>请尝试输入小区、写字楼或学校名</p>
              <p>详细地址（如门牌号）可稍后输入哦。</p>
          </div>
      </section>
  </div>
</template>

<script>
export default {
name:'SearchAddress',
data(){
    return{
        cityId:'',
        inputV:'',
        placeArr:[],
        isShow:true
    }
},
created(){
    this.cityId = localStorage.getItem("cityID")
},
methods:{
    searchAdd(){
        this.isShow = false;
        this.axios.get("/v1/pois",{
            params:{
                city_id:this.cityId,
                keyword:this.inputV,
                type:"search"
            }
        }).then(res=>{
            console.log(res)
            this.placeArr = res;
        }).catch(err=>{})
    },
    chenckAdd(p){
        this.inputV = p.name;
        this.$router.push('/addaddress');
        localStorage.setItem("Address",p.name+'('+p.address+')')
    }
}
}
</script>

<style scoped>
a{
    text-decoration: none;
}
.adddetailWrap{
    padding-top: 1.95rem;
}
header>a{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
.point{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
}
.point p{
    width: 100%;
    text-align: center;
    font-size: .7rem;
    color: #969696;
    margin-bottom: .4rem;
}
.warnpart{
    background: #fff6e4;
    font-size: .62rem;
    color: #ff883f;
    text-align: center;
    padding: .2rem 0;
}
.add-detail{
    display: flex;
    justify-content: space-between;
    background: #fff;
    padding: .5rem;
}
.add-detail input{
    display: block;
    width: 11.8rem;
    padding: .4rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: .6rem;
    outline: none;
}
.add-detail button{
    display: block;
    width: 3rem;
    background: #3199e8;
    font-size: .7rem;
    color: #fff;
    border-radius: 5px;
    margin-left: .1rem;
}
.poisearch-container{
    background: #f2f2f2;
}
.poisearch-container li{
    border-bottom: 1px solid #ccc;
    padding: .4rem;
}
.poisearch-container li p{
    font-size: .65rem;
    color: #969696;
    font-family: Helvetica Neue,Tahoma,Arial;
}
.p1{
    margin-bottom: .2rem;
}
</style>